*{
padding: 0;
margin: 0;
box-sizing: border-box;

}

.container{
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%,-50%);
    min-height: 40rem ;
    width: 30rem;
     
 }
figure{
    position: relative;
    top:30px;
    text-align: center;
}
figure h3{
font-family: Verdana, Geneva, Tahoma, sans-serif;
font-size: 15px;
font-weight: 600;

}
input[type=text]{
    padding: 10px;
    font-size: 15px;
    width: 80%;
    border: 2px solid rgb(255, 255, 255);
    background-color: rgb(255, 255, 255);
    border-radius: 20px;
    

}

.addItem{
    position: relative;
    top: 50px;
   left:50px;
   
}

.ItemsList:hover{
  background-image:var(--btn);
}
.plus{
    position: relative;
    font-size: 15px;
    top: 1px;
    right:40px;
}
.plus:hover{
cursor: pointer;
color:green;
transform: rotate(0.25turn);
transition: all ease-in-out 0.1s ;
}
.ItemsList{
    position: relative;
    top:80px;
    left: calc(25% - 80px);
    height: 50px;
    width: 80%;
    padding:10px;
    margin: 10px;
    font-size: 15px;
    border-radius: 10px;
    color:var(--primarycolor);
    background-image: var(--secondary);
}

.deletebtn{
    position: relative;
    float: right;
    bottom: 20px;
    font-size: 18px;
    
}
.Editbtn{
  position: relative;
  left:85%;
  bottom: 20px;
  font-size: 18px;
}
.deletebtn:hover ,.Editbtn:hover{
    color:rgb(255, 0, 0);
    cursor: pointer;
  }
  .btndiv{
    position: relative;
    left: calc(50% - 50px);
    top: 100px;
  }
  .btn{
    position:fixed;
    background-image: var(--btn);
    color:var(--primarycolor);
    padding:15px;
    border:none;
    border-radius: 20px;

  }
  .btn:hover{
    background-image: var(--primarycolor);
    color: rgb(214, 58, 249);
    border:1px solid rgb(214, 58, 249) ;
  }
  /*animated text css*/
  .wrapper{
    display: flex;
    position: relative;
    left:8rem;
  }
  .wrapper .static-txt{
    color: #fff;
    font-size: 60px;
    font-weight: 400;
  }
  .wrapper .dynamic-txts{
    margin-left: 15px;
    height: 90px;
    line-height: 90px;
    overflow: hidden;
  }
  .dynamic-txts li{
    list-style: none;
    color: #FC6D6D;
    font-size: 60px;
    font-weight: 400;
    position: relative;
    top: 0;
    animation: slide 8s steps(4) infinite;
  }
  @keyframes slide {
    100%{
      top: -360px;
    }
  }
  .dynamic-txts li span{
    position: relative;
    margin: 5px 0;
    line-height: 90px;
  }
  .dynamic-txts li span::after{
    content: "";
    position: absolute;
    left: 0;
    height: 100%;
    width: 100%;
    background: #060029;
    border-left: 2px solid #FC6D6D;
    animation: typing 2s steps(5) infinite;
  }
  @keyframes typing {
    40%, 60%{
      left: calc(100% + 30px);
    }
    100%{
      left: 0;
    }
  }